import { SPHttpClient, SPHttpClientResponse } from '@microsoft/sp-http';
import {ListItemFactory} from './ListItemFactory';
import {IListItem} from './models/IListItem';
import {INewsListItem} from './models/INewsListItem';

export class NewsListItemFactory extends ListItemFactory {    
    public _getItems(requester: SPHttpClient, siteUrl: string, listName: string): INewsListItem[] {
        let items: INewsListItem[];
        requester.get(`${siteUrl}/_api/web/lists/getbytitle('${listName}')/items?$select=Title,Id`,
        SPHttpClient.configurations.v1,
        {
            headers: {
            'Accept': 'application/json;odata=nometadata',
            'odata-version': ''
            }
        })
        .then((response: SPHttpClientResponse): Promise<{ value: INewsListItem[] }> => {
            return response.json();
        })
        .then((response: { value: INewsListItem[] }): void => {
            items= response.value;
        });
        return items;
    }
}